import React, {useState} from 'react'
import classes from './styles/dialogsField.module.css'
import DialogItem from './DialogItem'
import menu from '../menu.svg'
import Search from './Search'

const DialogsField = ({setDialog, dialogs, currentDialog, setTalk, setLoggedOut}) => {
    // const [_dialogs, setDialogs] = useState(dialogs)
    const [width, setWidth] = useState(30 + '%')

    const changeDialog = (id) => {
        setDialog(id)
        setTalk(null)
        console.log(id)
    }
    
    return (
        <div className={classes.Dialogs} style={{width: width}}>
            <div className={classes.searchBox}>
                <div className={classes.dialogFieldSBox}>
                    <h2>Search</h2> 
                    <div className={classes.dropDownMenu}>
                        <button  className={classes.dropDownMenuButton}><img src={menu}/></button>
                    </div>
                    
                </div>
                <a href="/un_authorize" className={classes.logOutBtn} onClick={() => setLoggedOut(false)}>Log Out</a>
                
                <Search />
            </div>
            <div className={classes.DialogList}>
                {dialogs.map((post, index)=> (
                    <DialogItem 
                        key={post.id}
                        id={post.id}
                        index={index}
                        name={post.name}
                        lastTalk={post.talks[post.talks.length - 1].name}
                        onclick={changeDialog}
                        current={currentDialog === index? true : false}/>
                ))}
            </div>
        </div>
    )
}

export default DialogsField;
